package com.sefagurel.dynamiccomponent.component

import androidx.compose.foundation.Image
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.LazyRow
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import com.sefagurel.dynamiccomponent.rememberImagePainter

@Composable
internal fun DynamicComponent(
    model: ComponentUIModel,
    scope: Any = Any()
) {
    when (model) {
        is BoxUIModel -> BoxComponent(model, scope)
        is ColumnUIModel -> ColumnComponent(model, scope)
        is LazyColumnUIModel -> LazyColumnComponent(model, scope)
        is RowUIModel -> RowComponent(model, scope)
        is LazyRowUIModel -> LazyRowComponent(model, scope)
        is TextUIModel -> TextComponent(model, scope)
        is ImageUIModel -> ImageComponent(model, scope)
    }
}

@Composable
internal fun BoxComponent(
    model: BoxUIModel,
    scope: Any = Any()
) {
    Box(
        modifier = model.modifier.createModifiers(scope),
        contentAlignment = model.contentAlignment,
        propagateMinConstraints = model.propagateMinConstraints,
        content = {
            model.children.forEach {
                DynamicComponent(it, this)
            }
        }
    )
}

@Composable
internal fun ColumnComponent(
    model: ColumnUIModel,
    scope: Any = Any()
) {
    Column(
        modifier = model.modifier.createModifiers(scope),
        verticalArrangement = model.verticalArrangement,
        horizontalAlignment = model.horizontalAlignment,
        content = {
            model.children.forEach {
                DynamicComponent(it, this)
            }
        }
    )
}

@Composable
internal fun LazyColumnComponent(
    model: LazyColumnUIModel,
    scope: Any = Any()
) {
    LazyColumn(
        modifier = model.modifier.createModifiers(scope),
        verticalArrangement = model.verticalArrangement,
        horizontalAlignment = model.horizontalAlignment,
        content = {
            model.children.forEach {
                item {
                    DynamicComponent(it, this)
                }
            }
        }
    )
}

@Composable
internal fun RowComponent(
    model: RowUIModel,
    scope: Any = Any()
) {
    Row(
        modifier = model.modifier.createModifiers(scope),
        verticalAlignment = model.verticalAlignment,
        horizontalArrangement = model.horizontalArrangement,
        content = {
            model.children.forEach {
                DynamicComponent(it, this)
            }
        }
    )
}

@Composable
internal fun LazyRowComponent(
    model: LazyRowUIModel,
    scope: Any = Any()
) {
    LazyRow(
        modifier = model.modifier.createModifiers(scope),
        verticalAlignment = model.verticalAlignment,
        horizontalArrangement = model.horizontalArrangement,
        content = {
            model.children.forEach {
                item {
                    DynamicComponent(it, this)
                }
            }
        }
    )
}

@Composable
internal fun TextComponent(
    model: TextUIModel,
    scope: Any = Any()
) {
    Text(
        text = model.text,
        modifier = model.modifier.createModifiers(scope),
        color = model.color,
        fontSize = model.fontSize,
        fontStyle = model.fontStyle,
        fontWeight = model.fontWeight,
        letterSpacing = model.letterSpacing,
        lineHeight = model.lineHeight,
        textDecoration = model.textDecoration,
        textAlign = model.textAlign,
        fontFamily = model.fontFamily,
        softWrap = model.softWrap,
        maxLines = model.maxLines,
        overflow = model.overflow
    )
}

@Composable
internal fun ImageComponent(
    model: ImageUIModel,
    scope: Any = Any()
) {
    Image(
        painter = rememberImagePainter(model.url),
        contentDescription = model.contentDescription,
        modifier = model.modifier.createModifiers(scope),
        alignment = model.alignment,
        contentScale = model.contentScale,
        alpha = model.alpha
    )
}

